    sketch.text(formatTime(time), size.x / 2, 10);

    // cursor
    sketch.push();
    sketch.stroke(255);
    sketch.strokeWeight(5);
    sketch.translate(size.x / 2 + mouse.x, size.y / 2 + mouse.y);
